<template>
  <div class="admin-registration-step-2">
    <AuthTitle class="admin-registration-step-2__title">
      {{ tp('setPassword') }}
    </AuthTitle>
    <form @submit.prevent class="admin-registration-step-2__form">
      <div class="admin-registration-step-2__form__item">
        <Input
          v-model="form.password.value"
          :placeholder="tp('passwordPlaceholder')"
          hide-shadow
          bordered
          type="password"
          @update:model-value="form.password.validate"
        >
          <template #prepend>
            <Icon name="password" />
          </template>
          <template v-if="form.password.value" #append>
            <Icon :name="form.password.error ? 'red-close' : 'green-checked'" />
          </template>
        </Input>
      </div>
      <div class="admin-registration-step-2__form__item">
        <Input
          v-model="form.confirmPassword.value"
          :placeholder="t('social.confirmPassword')"
          hide-shadow
          bordered
          type="password"
          @update:model-value="form.confirmPassword.validate"
        >
          <template #prepend>
            <Icon name="password" />
          </template>
        </Input>
      </div>
      <Text
        :style="{
          opacity: errorText ? 1 : 0
        }"
        variant="p"
        class="admin-registration-step-2__error"
        size="14px"
        line-height="16px"
        mb="33px"
      >{{ errorText || '0' }}</Text>
      <Button
        class="admin-registration-step-2__submit"
        button-type="submit"
        type="primary"
        @click="onSubmit"
      >
        <Text
          weight="400"
          size="14px"
          line-height="16px"
          color="white"
        >{{ tp('updatePassword')}}</Text>
      </Button>
    </form>
  </div>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { computed, reactive } from 'vue'
import Icon from '@/shared/components/Icon/Icon'
import Text from '@/shared/components/UIKit/Text/Text'
import Input from '@/shared/components/UIKit/Input'
import Button from '@/shared/components/UIKit/Button'
import AuthTitle from '@/admin/views/Auth/AuthTitle'

export default {
  name: 'Step2',
  components: { Text, Button, Icon, Input, AuthTitle },
  emits: ['submit'],
  setup (_, { emit }) {
    const { tp, t } = useI18n('adminAuth.resetPassword')

    const form = reactive({
      password: {
        value: '898989',
        error: false,
        errorText: t('social.weakPassword'),
        validate: () => {
          form.password.error = form.password.value.length < 8
          return form.password.error
        }
      },
      confirmPassword: {
        value: '',
        error: false,
        errorText: t('social.notMatch'),
        validate: () => {
          form.confirmPassword.error = form.password.value !== form.confirmPassword.value
          return form.confirmPassword.error
        }
      }
    })

    const errorText = computed(() => {
      if (form.password.error) {
        return form.password.errorText
      }
      if (form.confirmPassword.error) {
        return form.confirmPassword.errorText
      }
      return ''
    })

    const onSubmit = () => {
      if (validate()) {
        return
      }
      emit('submit', form)
    }
    const validate = () => {
      form.password.validate()
      form.confirmPassword.validate()

      return form.password.error || form.confirmPassword.error
    }

    return {
      t,
      tp,
      form,
      errorText,
      onSubmit
    }
  }
}
</script>

<style lang="scss">

.admin-registration-step-2 {
  padding: 40px 55px 0 55px;

  &__title {
    margin-bottom: 30px;
  }

  &__form {

    &__item {
      margin-bottom: 17px;

      &:last-of-type {
        margin-bottom: 12px;
      }
      .input {
        padding: 14px 17px;
      }
    }
  }
  &__submit {
    width: 100%;
  }
  &__error {
    text-align: center;
    color: $adminErrorColor;
  }
}
</style>
